}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  border: 2px solid red; 
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Arvo', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
  font-size: 1.5em;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;                                               
}

.contact-list {
